@use 'base/variables' as v;
@use 'base/mixins' as m;

.titulo{
    color: v.$rosa;
    text-align: center;
    padding: 4rem;
}

.listado-movimientos{
    width: 95%;
    margin: 0 auto;

    @include m.tablet{
        width: 65%;
    }

    .movimiento{
        background-color: v.$rosaClaro;
        a{
            color: v.$blanco;
            font-weight: 600;
            margin: 0;
            font-size: 1.5rem;
        }
    }
    padding-bottom: 4rem;

    .boton{
        margin-bottom: 2rem;
    }

}
.eliminar{
    margin: 0 auto;
    @include m.boton(v.$rojo, v.$blanco);
    width: 70%;
    @include m.tablet{
        width: 20%;
    }
}

.boton-2{
    width: 70%;
    @include m.tablet{
        width: 20%;
    }
    margin-bottom: 2rem;
}