@use 'variables' as v;
@use 'base/mixins' as m;

*, *:before, *:after {
    box-sizing: inherit;
}
html {
    font-size: 62.5%;
    box-sizing: border-box;
    height: 100%;
}
body {
    font-family: v.$fuente_textos;
    font-size: 1.6rem;
    min-height: 100%;
    @include m.tablet {
        overflow: scroll;
        overflow-x: hidden;
        margin-right: 0;
    
        &::-webkit-scrollbar {
            width: 0;
        }
    
        &::-webkit-scrollbar-thumb {
            background-color: rgba(255,255,255, .4);
            border-radius: 5px;
        }
    }
    

}
.contenedor {
    width: 95%;
    max-width: 1200px;
    margin: 0 auto;


}
.contenedor-sm {
    max-width: 70rem;
    margin: 0 auto;
}
a {
    text-decoration: none;
}
img {
    max-width: 100%;
}
