@use 'base/variables' as v;

.alerta{
    padding: 1rem 1rem 1rem 2rem;
    font-weight: 900;
    margin: 1rem 0;
    font-size: 1.2rem;
    max-width: 80rem;
    border-radius: 3rem;
    
    &.error{
        background-color: lighten(v.$rosa, 40%);
        border-left: .5rem solid v.$rosa;
        color: v.$rosa;
    }

    &.exito{
        background-color: lighten(v.$verde, 60%);
        border-left: .5rem solid v.$verde;
        color: v.$verde;
    }


}