@use 'base/variables' as v;
@use 'base/mixins' as m;

.formulario{
    margin-top: 5rem;
    .campo{
        display: flex;
        align-items: center;
        margin-bottom: 2rem;


        label{
            flex: 0 0 10rem;
            display: none;
            @include m.tablet{
                display: flex;
            }
        }
        input, select{
            flex: 0.9;
            padding: 1rem;
            border: none;
            background-color: lighten(v.$gris, 40%);
            margin: 0 auto;
            border-radius: 1.4rem;
            line-height: 1.5rem;

            @include m.tablet{
                flex: 1;
                margin: 0;
            }
        }
    }

}