html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
}
@media (min-width: 768px) {
  body {
    overflow: scroll;
    overflow-x: hidden;
    margin-right: 0;
  }
  body::-webkit-scrollbar {
    width: 0;
  }
  body::-webkit-scrollbar-thumb {
    background-color: rgba(255, 255, 255, 0.4);
    border-radius: 5px;
  }
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.contenedor-sm {
  max-width: 70rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Montserrat", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 5rem;
}

h3 {
  font-size: 4rem;
}

p {
  font-size: 2rem;
  color: #6b7280;
  line-height: 1.8;
}

.login {
  margin-top: 5rem;
}
.login .becabud {
  color: #DB2777;
  text-align: center;
}
.login .becabud span {
  color: #7C3AED;
}
.login input[type=submit] {
  background-color: #7C3AED;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  border-radius: 3rem;
  width: 90%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .login input[type=submit] {
    width: auto;
  }
}
.login input[type=submit]:hover {
  background-color: #5f14e0;
  cursor: pointer;
}
@media (min-width: 768px) {
  .login input[type=submit] {
    width: auto;
    margin: 0;
  }
}
.login .formulario {
  width: 100%;
  margin: 0 auto;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .login .formulario {
    width: 50%;
  }
}

.formulario {
  margin-top: 5rem;
}
.formulario .campo {
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
}
.formulario .campo label {
  flex: 0 0 10rem;
  display: none;
}
@media (min-width: 768px) {
  .formulario .campo label {
    display: flex;
  }
}
.formulario .campo input, .formulario .campo select {
  flex: 0.9;
  padding: 1rem;
  border: none;
  background-color: #d8dadf;
  margin: 0 auto;
  border-radius: 1.4rem;
  line-height: 1.5rem;
}
@media (min-width: 768px) {
  .formulario .campo input, .formulario .campo select {
    flex: 1;
    margin: 0;
  }
}

.alerta {
  padding: 1rem 1rem 1rem 2rem;
  font-weight: 900;
  margin: 1rem 0;
  font-size: 1.2rem;
  max-width: 80rem;
  border-radius: 3rem;
}
.alerta.error {
  background-color: #f8d6e5;
  border-left: 0.5rem solid #DB2777;
  color: #DB2777;
}
.alerta.exito {
  background-color: #abffab;
  border-left: 0.5rem solid rgb(0, 120, 0);
  color: rgb(0, 120, 0);
}

.header {
  background-color: #DB2777;
  margin: 0;
  border-radius: 0 0 2.5rem 2.5rem;
  -webkit-box-shadow: 0px 5px 14px 1px rgba(0, 0, 0, 0.45);
  -moz-box-shadow: 0px 5px 14px 1px rgba(0, 0, 0, 0.45);
  box-shadow: 0px 5px 14px 1px rgba(0, 0, 0, 0.45);
  max-width: 100%;
  margin: 0 auto;
  padding-bottom: 2rem;
}
.header .info {
  margin: 0;
  color: #FFFFFF;
  font-weight: 700;
  font-size: 1.4rem;
}
@media (min-width: 768px) {
  .header .info {
    font-size: 2rem;
  }
}
.header .contenedor-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 95%;
  margin: 0 auto;
  padding: 2rem 0 0 0;
}
.header .boton {
  padding: 0.8rem 1rem;
}
.header .header-otros {
  display: block;
}
@media (min-width: 768px) {
  .header .header-otros {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 1rem;
  }
}

.boton {
  background-color: #380C83;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  border-radius: 3rem;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .boton {
    width: auto;
  }
}
.boton:hover {
  background-color: #240854;
  cursor: pointer;
}
@media (min-width: 768px) {
  .boton {
    max-width: 98%;
  }
}
@media (min-width: 1024px) {
  .boton {
    max-width: 70%;
  }
}

.secciones {
  display: block;
  padding-bottom: 9rem;
}
@media (min-width: 768px) {
  .secciones {
    width: 99%;
    margin: 0 auto;
    display: flex;
    gap: 4rem;
    justify-content: center;
    padding-bottom: 9rem;
  }
}

.seccion {
  min-width: 45%;
  max-width: 95%;
  margin: 0 auto;
  margin-top: 4rem;
  color: #FFFFFF;
  padding: 1.5rem;
  border-radius: 4rem;
}
@media (min-width: 768px) {
  .seccion {
    margin: 2rem 0 0 0;
  }
}
.seccion h3 {
  font-size: 2.4rem;
  text-align: center;
}
.seccion.movimientos {
  background-color: #6A1FEA;
  display: flex;
  flex-direction: column;
}
.seccion.beca-detalle {
  background-color: #F6AA28;
}

.movimiento {
  -webkit-box-shadow: 0px 5px 14px -6px rgba(0, 0, 0, 0.45);
  -moz-box-shadow: 0px 5px 14px -6px rgba(0, 0, 0, 0.45);
  box-shadow: 0px 5px 14px -6px rgba(0, 0, 0, 0.45);
  background-color: #9B6AF1;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
  padding: 1rem;
  border-radius: 2rem;
}
.movimiento p {
  color: #FFFFFF;
  font-weight: 600;
  font-size: 2.2rem;
  margin: 0;
}
.movimiento .info-movimiento p {
  margin: 0;
  font-size: 1.5rem;
}
.movimiento a {
  color: #FFFFFF;
  font-weight: 600;
  margin: 0;
  font-size: 1.5rem;
}

.info-beca {
  display: flex;
  justify-content: space-between;
  background-color: #F9C976;
  border-radius: 2rem;
  margin-bottom: 1.5rem;
  -webkit-box-shadow: 0px 5px 14px -6px rgba(0, 0, 0, 0.45);
  -moz-box-shadow: 0px 5px 14px -6px rgba(0, 0, 0, 0.45);
  box-shadow: 0px 5px 14px -6px rgba(0, 0, 0, 0.45);
}
.info-beca p {
  color: #FFFFFF;
  font-weight: 600;
  font-size: 2rem;
  margin: 0;
  padding: 1rem;
  line-height: 2.5rem;
}

.boton-dash {
  max-width: 90%;
  margin-top: 1rem;
}
@media (min-width: 768px) {
  .boton-dash {
    max-width: 50%;
  }
}

body {
  padding-bottom: 1rem;
}

.titulo {
  color: #DB2777;
  text-align: center;
  padding: 4rem;
}

.listado-movimientos {
  width: 95%;
  margin: 0 auto;
  padding-bottom: 4rem;
}
@media (min-width: 768px) {
  .listado-movimientos {
    width: 65%;
  }
}
.listado-movimientos .movimiento {
  background-color: #E773A8;
}
.listado-movimientos .movimiento a {
  color: #FFFFFF;
  font-weight: 600;
  margin: 0;
  font-size: 1.5rem;
}
.listado-movimientos .boton {
  margin-bottom: 2rem;
}

.eliminar {
  margin: 0 auto;
  background-color: rgb(169, 0, 0);
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  border-radius: 3rem;
  width: 70%;
}
@media (min-width: 768px) {
  .eliminar {
    width: auto;
  }
}
.eliminar:hover {
  background-color: #760000;
  cursor: pointer;
}
@media (min-width: 768px) {
  .eliminar {
    width: 20%;
  }
}

.boton-2 {
  width: 70%;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .boton-2 {
    width: 20%;
  }
}