@use 'base/variables' as v;
@use 'base/mixins' as m;

.header{
    background-color: v.$rosa;
    margin: 0;
    border-radius: 0 0 2.5rem 2.5rem;
    -webkit-box-shadow: 0px 5px 14px 1px rgba(0,0,0,0.45);
    -moz-box-shadow: 0px 5px 14px 1px rgba(0,0,0,0.45);
    box-shadow: 0px 5px 14px 1px rgba(0,0,0,0.45);
    max-width: 100%;
    margin: 0 auto;
    padding-bottom: 2rem;
    .info{
        margin: 0;
        color: v.$blanco;
        font-weight: 700;
        font-size: 1.4rem;
        @include m.tablet{
            font-size: 2rem;
        }
    }

    .contenedor-header{
        display: flex;
        justify-content: space-between;
        align-items: center;
        max-width: 95%;
        margin: 0 auto;
        padding: 2rem 0 0 0;
    }

    .boton{
        padding: 0.8rem 1rem;
    }

    .header-otros{
        display: block;
        @include m.tablet{
            display: flex;
            align-items: center;
            justify-content: center;
            gap: 1rem;
        }
    }
}