@use 'base/variables' as v;
@use 'base/mixins' as m;


.boton{
    @include m.boton(v.$moradoBoton, v.$blanco);
    @include m.tablet{
        max-width: 98%;
    }
    @include m.desktop{
        max-width: 70%;
    }
    margin: 0 auto;
}
.secciones{
    display: block;
    @include m.tablet{
        width: 99%;
        margin: 0 auto;
        display: flex;
        gap: 4rem;
        justify-content: center;
        padding-bottom: 9rem;
    }
    padding-bottom: 9rem;
}

.seccion{
    min-width: 45%;
    max-width: 95%;
    margin: 0 auto;
    @include m.tablet{
        margin: 2rem 0 0 0;
    }
    margin-top: 4rem;
    color: v.$blanco;
    h3{
        font-size: 2.4rem;
        text-align: center;
    }
    padding: 1.5rem;
    border-radius: 4rem;
    &.movimientos{
        background-color: v.$fondoMovimientos;
        display: flex;
        flex-direction: column;
    }
    &.beca-detalle{
        background-color: v.$fondoBecaDetalle;
    }


}    


.movimiento{
        -webkit-box-shadow: 0px 5px 14px -6px rgba(0,0,0,0.45);
        -moz-box-shadow: 0px 5px 14px -6px rgba(0,0,0,0.45);
        box-shadow: 0px 5px 14px -6px rgba(0,0,0,0.45);
        background-color: v.$fondoMovimiento;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 1.5rem;
        padding: 1rem;
        border-radius: 2rem;
        p{
            color: v.$blanco;
            font-weight: 600;
            font-size: 2.2rem;
            margin: 0;
        }

        .info-movimiento{
            p{
                margin: 0;
                font-size: 1.5rem;
            }
        }
        a{
            color: v.$blanco;
            font-weight: 600;
            margin: 0;
            font-size: 1.5rem;
        }
}

.info-beca{
    display: flex;
    justify-content: space-between;
    background-color: v.$fondoInfoBeca;
    border-radius: 2rem;
    margin-bottom: 1.5rem;
    -webkit-box-shadow: 0px 5px 14px -6px rgba(0,0,0,0.45);
    -moz-box-shadow: 0px 5px 14px -6px rgba(0,0,0,0.45);
    box-shadow: 0px 5px 14px -6px rgba(0,0,0,0.45);

    p{
        color: v.$blanco;
        font-weight: 600;
        font-size: 2rem;
        margin: 0;
        padding: 1rem;
        line-height: 2.5rem;
        
 
    }


}

.boton-dash{
    max-width: 90%;
    @include m.tablet{
        max-width: 50%;
    }
    margin-top: 1rem;
}
body{
    padding-bottom: 1rem;
}