@use 'base/variables' as v;
@use 'base/mixins' as m;


.login{
    margin-top: 5rem;

    .becabud{
        color: v.$rosa;

        span{
            color: v.$morado;
        }
        text-align: center;
    }

    input[type="submit"]{
        @include m.boton(v.$morado, v.$blanco);

        width: 90%;
        margin: 0 auto;

        @include m.tablet{
            width: auto;
            margin: 0;
        }
    }


    .formulario{
        width: 100%;
        margin: 0 auto;
        margin-top: 5rem;
        @include m.tablet{
            width: 50%;
        }
    }
}